@import url(https://fonts.googleapis.com/css2?family=Lexend:wght@400;500;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lexend:wght@400;500;600&display=swap);
body {
  margin: 0;
  font-family: "Lexend", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #ada996; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #eaeaea,
    #dbdbdb,
    #f2f2f2,
    #ada996
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  padding: 10px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
  font-family: "Lexend", sans-serif;
}

.card {
  background-color: cornsilk;
  margin: 20px;
  display: inline-block;
  text-align: center;
  border-radius: 33px;
  max-width: 250px;

  transition: -webkit-transform 300ms ease-in-out box-shadow 300ms ease-in-out;

  transition: transform 300ms ease-in-out box-shadow 300ms ease-in-out;

  transition: transform 300ms ease-in-out box-shadow 300ms ease-in-out, -webkit-transform 300ms ease-in-out box-shadow 300ms ease-in-out;
}
.card img {
  width: 75%;
  margin: 0 auto;
  object-fit: cover;
}

.card:hover {
  cursor: pointer;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  box-shadow: 1px 1px 1px rgb(180, 172, 141);
}

div {
  padding-bottom: 20px;
}

input {
  padding: 10px 20px;
  border: none;
  outline: none;
  font-family: "Lexend", sans-serif;
  background-color: rgb(189, 214, 189);
}

.scroll {
  overflow-y: scroll;
  height: 600px;
  border: none;
}

.body-main {
  text-align: center;
}

h1 {
  font-size: 2.5rem;
  color: rgb(20, 48, 20);
  text-transform: uppercase;
}

